* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
   /* Spacing */
  /* 1rem = 16px */
  --space-0: 0;
  --space-1: 0.25rem;
  --space-2: 0.5rem;
  --space-3: 0.75rem;
  --space-4: 1rem;
  --space-5: 1.25rem;
  --space-6: 1.5rem;
  --space-8: 2rem;
  --space-10: 2.5rem;
  --space-12: 3rem;
  --space-16: 4rem;
  --space-20: 5rem;
  --space-24: 6rem;

  /* Font Size */

  --text-sm: 0.875rem;
  --text-md: 1rem;
  --text-lg: 1.25;
  --text-xlg: 1.5rem;

  /* Border radius */
  --radius: 6px;
  --round: 1000px;

  /* Border */
  --border: 1px solid var(--color-border);

  /* Shadow */
  --shadow: 0 2px 8px rgba(0,0,0,0.06), 0 2px 3px rgba(0,0,0,0.05);
  --shadow-focus: 0 0 0 4px rgba(140,26,26,0.9);
  --shadow-large: 0px 5px 18px rgba(0, 0, 0, 0.1),
                    0px 1px 3px rgba(0, 0, 0, 0.05);

  /* Transition */
  --transition: all 1s var(--transition-curve);
  --transition-bounce: all var(--transition-speed) var(--transition-curve-bounce);

  /* Transitions curve*/
  --transition-curve: cubic-bezier(0.2,0.7,0.3,1);
  --transition-curve-bounce: cubic-bezier(0.175, 0.885, 0.32, 1.275);

  /* Transition speed */
  --transition-speed: .25s;
  --transition-speed-slow: 1s;

  /* Opacities */
  --opacity-25: 0.25;
  --opacity-50: 0.50;
  --opacity-75: 0.75;
  --opacity-100: 1.00;

}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #8C1A1A;
  
  transition: var(--transition);
}

.background-color{
  background-color: #8C1A1A;
}

.color{
  color:  #8C1A1A
}

#quote-box{

  width: 400px;
  padding: var(--space-4);
  background-color: white;
  border-radius: 6px;

 
}

.fadeOut{
  /* animation: fadeOut var(--transition-speed) var(--transition-curve); */
  transition: var(--transition);
  opacity: 0;
}


@keyframes fadeOut{
  to{
    opacity: 0;
  }
}
#author{
  display:block;
  /* margin-left:auto; */
  font-size: var(--text-sm);
  margin-top: var(--space-1);
  margin-left: var(--space-1);
}

.box{
  display: flex;
  flex-direction: column;
  transition: var(--transition);
}

.buttons{
  display: flex;
}

button{
  border:0;
  border-radius: 6px;
  cursor: pointer;
  background-color: #8C1A1A;
  color: white;
  padding: var(--space-2);
  margin-top: var(--space-4);
}

button:hover{
  background-color: rgba(140,26,26,0.9);
}
button:focus{
  
  outline:none;
}

a{
  outline:none;
}


#tumblr,#tweet-quote{
  width: 30px;
}

#tweet-quote{
  margin-right: var(--space-2);
}
#new-quote{
  margin-left: auto;
}